/* Reset CSS */
* {
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video, main {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

/* Main styles */
html {
  min-height: 100%;
}

body {
  min-height: 100%;
  position: relative;
  background: #fdf5e6;
  font: 1rem/1.3 Arial, sans-serif;
  color: #222;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  vertical-align: top;
  max-width: 100%;
  height: auto;
}

a {
  color: #222;
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
  transition: color 0.3s ease;
}
a:hover {
  color: #6667AB;
}

p {
  margin: 0 0 1rem;
}

button {
  cursor: pointer;
}

/* Titles fonts */
h1 {
  font-size: 2.75rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h1 mark,
h1 a,
h2 mark,
h2 a,
h3 mark,
h3 a {
  color: #6667AB;
  background: none;
  font-style: normal;
}

/* Main styles */
#wrapper {
  max-width: 100%;
  position: relative;
  min-height: 100%;
  overflow: hidden;
}